<template>
    <div class="myLogin">
         <span ref="logo" class="logos iconfont icon-wangyiyunyinle animate__animated"></span>

         <div ref="but01" class="but dlu animate__animated" @click="goPath('Phonelogin')">手机号登录</div>
         <div ref="but02" class="but lji animate__animated" @click="goPath('Find')">立即体验</div>


         <div ref="icons" class="icons animate__animated" >
             <span v-for="item in icons" :key="item" class="iconfont" :class="item" @click="ff_exploit"></span>
         </div>
         <p ref="ps">
             <van-checkbox v-model="checked" shape="square" checked-color="transparent">同意</van-checkbox>《用户协议》《隐私政策》《儿童隐私政策》
         </p>
    </div>
</template>

<script>
import '../assets/less/Login.less'
    export default {
        name : 'Login',
        data() {
            return {
                icons   : ['icon-weixin','icon-qq','icon-weibo','icon-wangyi'],
                checked : false,
                tim01   : null,
                tim02   : null,
            }
        },
        methods:{
            goPath(name){
                if(this.checked){
                   this.$router.push({name});  
                }else{
                    
                    clearTimeout(this.tim01);
                    clearTimeout(this.tim02)
                    this.$refs.ps.className =  'animate__animated animate__wobble'
                    this.tim01 =  setTimeout(()=>{
                        this.$toast('请同意有关政策');
                    },300)
                
                    this.tim02 = setTimeout(()=>{
                        this.$refs.ps.className = '';
                    },1500)
                }
            },
            ff_exploit(){
                  this.$toast.loading({
                    message: '   开发中  ',
                    forbidClick: true,
                    loadingType: 'spinner',
                  });

            },
            // 关闭播放器
            ff_isFlash(){ 
                // 暂停音乐
                if(this.music.myAudio){
                    this.ff_isPlay(false);
                    this.music.myAudio.pause();
                    this.$store.commit('gaiIsflash',false);

                }
                
    
            },
            // 修改按钮状态
            ff_isPlay(data){
                this.$store.dispatch('gaiPaly',data)
            },

        },
        created() {
            // 关闭播放器
             this.ff_isFlash();   
             
        },
        // fadeInUp
        mounted(){
             this.$refs.logo.classList.add('animate__rotateIn');
             this.$refs.but01.classList.add('animate__zoomInDown');
             this.$refs.but02.classList.add('animate__zoomInDown');
             this.$refs.icons.classList.add('animate__fadeInUp');
             setTimeout(()=>{
                 this.$refs.logo.classList.remove('animate__rotateIn');
                 this.$refs.but01.classList.remove('animate__zoomInDown');
                 this.$refs.but02.classList.remove('animate__zoomInDown');
                 this.$refs.icons.classList.remove('animate__fadeInUp');
             },1500)
        },
        
        computed: {
            music(){
                   return this.$store.state.song ;
            }
            
        },

    }
</script>

<style lang="less" scoped>

</style>